export default {
	data() {
		return {

		}
	},
	onLoad() {
		this.getCode()
		
	},
	methods: {
		// 截取
		getQueryString(name) {
			var url = window.location.href
			var after = url.split("?")[1];
			var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
			if (after) {
				var r = after.match(reg)
				if (r !== null) {
					console.log('截取code', decodeURIComponent(r[2]))
					return decodeURIComponent(r[2]);
				} else {
					var a = url.split("?")[2]
					if (a) {
						var s = a.match(reg)
						if (s !== null) {
							console.log('截取code', decodeURIComponent(s[2]))
							return decodeURIComponent(s[2]);
						} else {
							return null
						}
					}

				}
			}
		},
		getCode() {
			var inviteCode = '',
				inviteCode = this.getQueryString('inviteCode')
				console.log('合伙人链接截取的invitecode',inviteCode)
			if (!inviteCode) {
				console.log('合伙人从本地获取invitecode')
				// inviteCode = ''
				inviteCode = uni.getStorageSync('inviteCode')
			}
			console.log('合伙人最终获取的inviteCode', inviteCode)
			var redirectIp = this.$url.mainURL + '/app/partnerLogin'
			window.location.href = "https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx5136cb5c7d21f3d1&redirect_uri=" +
				redirectIp + "&response_type=code&scope=snsapi_userinfo&state=" + inviteCode + "#wechat_redirect"
		}
	}
}
